import serverUrl from '../../common/serveUrl.js'
export default {
	data() {
		return {
			anchorIndex: '1',
			isShow: true,
			url: '../../../static/mine_icon_head portrait.png',
			//个人信息
			meansList: [
			],
			//企业信息
			manageList: [
			],
		}
	},
	onLoad(options) {
		var id = options.id
	},
	methods: {
		//点击按钮变色
		btnClick(e) {
			if(e.target.dataset.index != 2 ){
			    this.anchorIndex = 1;
			}else{
			    //获取当前点击事件的下标
			    this.anchorIndex =  e.target.dataset.index
			}
			if (e.target.dataset.index == 2) {
				this.isShow = false
			} else {
				this.isShow = true
			}
		},
		prr() {
			var url = serverUrl.serverUrl;
			uni.request({
				url: url + '/app/user/getUserById',
				data: {
					id: id
				},
				methods: 'POST',
				success: res => {
					console.log(res)
					if (res.data.code == 200) {
						this.means = res.data.data
					}
				}
			})
		}
		
	}
}
